body
{
    background-color:#b5e5ff;
    font-family: 'Roboto';
}
.logo
{
    background-color: #0a0057;
}
.submit
{
    background-color: #0a0057;
}
.submit:hover
{
    background-color: #1500B8;
}
.menubar
{
    background-color: white;
}


/*------------------- for sign up page-----------*/

#password-constraint
{
    display: none;
}

.valid 
{
    color: green;
}
.valid::before 
{
    content: "✔";
}
.invalid 
{
    color: red;
}
.invalid::before 
{
    content: "✖";
}

#equal-email,#equal-mobile
{
    display:none;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
input[type=number] {
    -moz-appearance: textfield;
}


/*-------------------side bar----------------------*/

.sidepanel  {
  width: 0;
  position: fixed;
  z-index: 1;
  height: 100%;
  top:0;
  left:0;
  background-color: white;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sidepanel button {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 20px;
  color: black;
  display: block;
  transition: 0.3s;
  outline: 0;
}

.sidepanel button:hover {
  color: grey;
}

.sidepanel .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  outline: 0;
}

.openbtn {
  font-size: 20px;
  cursor: pointer;
  background-color: #111;
  color: white;
  padding: 10px 15px;
  border: none;
  outline: 0;
}

.openbtn:hover {
  background-color:#444;
}

#sel:hover,#sel:focus
{
  outline: none !important;
  box-shadow:none !important;
}